import React, { useContext, useState } from 'react';
import Backdrop from 'components/backdrop';
import Search from 'components/search';
import useData from '../../hooks/useData';
import { PageOption } from 'localStorage/localStorage';
import _ from 'lodash';

import './index.css';
import LocalStore from '../../localStorage';
import ErrorsContext from 'context/errors/ErrorsContext';

type ComponentWithDataProps = { pageName: PageOption };

const withCatalogData = <T,>(
  Component: React.ElementType,
  getData: (selector: string) => Promise<T[]>
) => {
  const CatalogWithData = ({ pageName }: ComponentWithDataProps) => {
    const localStore = new LocalStore();
    const initialSearchValue = localStore.getValue(pageName) ?? 'The lord of the rings';
    const [searchValue, setSearchValue] = useState(initialSearchValue);

    const upadteLocalStoreValue = (value: string) => localStore.updateValue(value, pageName);

    const { onError } = useContext(ErrorsContext);

    const { data, isLoading } = useData<T>(getData, searchValue, upadteLocalStoreValue, onError);

    const handleSearchChange = (value: string) => {
      setSearchValue(value);
    };

    const handleSearchChangeLazy = _.debounce(handleSearchChange, 600);

    return (
      <main>
        <h1 className="catalog__title">{pageName === 'movieLand' ? 'MovieLand' : 'GameLand'}</h1>
        <Search onChange={handleSearchChangeLazy} value={searchValue} />
        {isLoading ? <Backdrop open={true} /> : <Component data={data} />}
      </main>
    );
  };
  return CatalogWithData;
};

export default withCatalogData;
