import React, { useState } from 'react';
import RouterView from './router';
import Header from './components/header';
import { BrowserRouter } from 'react-router-dom';
import Alert from 'components/alert/Alert';
import ErrorsContext from './context/errors/ErrorsContext';

function App() {
  const [errors, setErrors] = useState<Error[]>([]);

  const onErrorClick = (err: Error) => {
    const filteredErrors = errors.filter((error) => error !== err);
    setErrors(filteredErrors);
  };

  const onError = (newError: Error) => {
    setErrors([...errors, newError]);
  };

  return (
    <div className="my-app">
      <BrowserRouter basename="/react-gh-pages">
        <Header />
        <ErrorsContext.Provider value={{ errors, onError, onErrorClick }}>
          <RouterView />
          <Alert />
        </ErrorsContext.Provider>
      </BrowserRouter>
    </div>
  );
}

export default App;
