import { useEffect, useState } from 'react';

const useData = <T,>(
  getData: (selector: string) => Promise<T[]>,
  selector: string,
  updateLSValue: (value: string) => void,
  onError: (newError: Error) => void
): { data: T[] | null; isLoading: boolean } => {
  const [data, setData] = useState<T[] | null>(null);
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    setIsLoading(true);
    getData(selector)
      .then((result) => {
        setData(result);
        setIsLoading(false);
      })
      .catch((error: Error) => {
        onError(error);
        setData(null);
        setIsLoading(false);
      });
    return () => {
      updateLSValue(selector);
    };
  }, [selector]);
  return { data, isLoading };
};

export default useData;
