import React from 'react';
import { Link } from 'react-router-dom';
import './index.css';
import { IMovieShort } from '../../api/movieContracts';

export type CardProps = {
  movie: IMovieShort;
};

const Card = ({ movie }: CardProps) => {
  return (
    <div data-testid="card" className="card">
      <img className="card__image" src={movie.image_url} alt={movie.name} />
      <h1 className="card__title">{movie.name}</h1>
      <div className="card__descrep">{movie.year}</div>
      <Link to={`/movie/${movie.id}`} className="card__link">
        Read more
      </Link>
    </div>
  );
};

export default Card;
