import React, { useContext, useEffect, useState } from 'react';
import PagePresent from './PagePresentation';
import Backdrop from '../../components/backdrop';

import withRouter from 'components/highOrderComponents/WithRouter';
import { WithRouterProps } from 'router/routerContracts';

import { IMovieFull } from 'api/movieContracts';
import { getMovie } from 'api/movieApi';
import ErrorsContext from 'context/errors/ErrorsContext';

type PageProps = Record<string, string>;
const MoviePageContainer = ({ router }: WithRouterProps) => {
  const [movie, setMovie] = useState<IMovieFull | null>(null);

  const [open, setOpen] = useState(true);

  useEffect(() => {
    updateMovie();
  }, []);

  const { onError } = useContext(ErrorsContext);

  const updateMovie = (): void => {
    const { id } = router.params;

    getMovie(id ?? '')
      .then((movie) => {
        setMovie(movie);
        setOpen(false);
      })
      .catch(() => {
        setMovie(null);
        setOpen(false);
        const err = new Error("Cant't get movie detail");
        onError(err);
      });
  };

  return open ? <Backdrop open={true} /> : <PagePresent movie={movie} />;
};

export default withRouter<PageProps>(MoviePageContainer);
