import React from 'react';
import GameCard from '../gameCard';
import BackDrop from 'components/backdrop';
import DialogWindow from 'components/dialogWindow/DialogWindow';
import { IGame, IGameDetail } from 'api/gameContracts';

type GamesCatalogPresentationProps = {
  games: IGame[] | null;
  onCardClick: (id: number) => Promise<void>;
  onCloseModalWindow: () => void;
  isLoading: boolean;
  game: null | IGameDetail;
  isModalOpen: boolean;
};

const GamesCatalogPresentation = ({
  games,
  isLoading,
  game,
  isModalOpen,
  onCloseModalWindow,
  onCardClick,
}: GamesCatalogPresentationProps) => {
  return (
    <div className="container">
      <div role="gamesCatalog" className="games-catalog">
        {games ? (
          games.map((game) => <GameCard onCardClick={onCardClick} key={game.id} game={game} />)
        ) : (
          <h1 className="error__title">Nothing found</h1>
        )}
      </div>
      {isLoading && <BackDrop open={true} />}
      {!isLoading && (
        <DialogWindow game={game} isOpen={isModalOpen} onCLoseModalWindow={onCloseModalWindow} />
      )}
    </div>
  );
};

export default GamesCatalogPresentation;
