import React from 'react';
import { Modal } from 'antd';
import { IGameDetail } from 'api/gameContracts';
import { CardMedia, Link } from '@mui/material';

import './index.css';

type DialogWindowProps = {
  game: IGameDetail | null;
  isOpen: boolean;
  onCLoseModalWindow: () => void;
};

const DialogWindow = ({ game, isOpen, onCLoseModalWindow }: DialogWindowProps) => {
  return (
    <>
      {game && (
        <Modal
          title={game.name}
          open={isOpen}
          onOk={onCLoseModalWindow}
          onCancel={onCLoseModalWindow}
        >
          <CardMedia component="img" height="194" image={game.background_image} alt={game.name} />
          <p>Released: {game.released}</p>
          <p>Meritocratic: {game.metacritic}</p>
          <div>
            <hr />
            <h1>Genres</h1>
            {game?.genres.map((genre) => (
              <p key={genre.id}>{genre.name}</p>
            ))}
            <hr />
          </div>
          <div>
            <h1>Developers: </h1>
            {game?.developers.map((developer) => (
              <p key={developer.id}>{developer.name}</p>
            ))}
            <hr />
          </div>
          <Link href={game.website}>Website</Link>
        </Modal>
      )}
    </>
  );
};

export default DialogWindow;
