import React from 'react';
import Card from '../card';

import './index.css';

import { IMovieShort } from '../../api/movieContracts';
import { getMoviesList } from 'api/movieApi';
import withCatalogData from 'components/withCatalogData';

export type CatalogMoviesProps = {
  data: IMovieShort[] | null;
};

const CatalogMovies = ({ data }: CatalogMoviesProps) => {
  return (
    <div className="container">
      <div role="moviesCatalog" className="movies-catalog">
        {data ? (
          data.map((movie) => <Card key={movie.id as number} movie={movie} />)
        ) : (
          <h1 className="error__title">Nothing found</h1>
        )}
      </div>
    </div>
  );
};

export { CatalogMovies };

export default withCatalogData<IMovieShort>(CatalogMovies, getMoviesList);
