import React, { useContext, useState } from 'react';
import GamesCatalogPresentation from './GamesCatalogPresentation';

import withCatalogData from 'components/withCatalogData';

import { IGame, IGameDetail } from 'api/gameContracts';
import { getGame, getGamesList } from 'api/gameApi';

import './index.css';
import ErrorsContext from 'context/errors/ErrorsContext';

type GamesCatalogContainerProps = {
  data: IGame[] | null;
};

const GameCatalogContainer = ({ data }: GamesCatalogContainerProps) => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [gameDetail, setGameDetail] = useState<IGameDetail | null>(null);

  const toggleIsModalOpen = () => setIsModalOpen(!isModalOpen);

  const { onError } = useContext(ErrorsContext);

  const updateGame = async (id: number) => {
    setIsLoading(true);
    try {
      const game = await getGame(id);
      setGameDetail(game);
      setIsLoading(false);
    } catch {
      const err = new Error("Can't get gameDetail");
      onError(err);
      setGameDetail(null);
      setIsLoading(false);
    }
  };

  const onCardClick = async (id: number) => {
    await updateGame(id);
    toggleIsModalOpen();
  };

  return (
    <GamesCatalogPresentation
      games={data}
      game={gameDetail}
      onCardClick={onCardClick}
      onCloseModalWindow={toggleIsModalOpen}
      isModalOpen={isModalOpen}
      isLoading={isLoading}
    />
  );
};

export default withCatalogData<IGame>(GameCatalogContainer, getGamesList);
